html {
  font-size: 100%;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  height: var(--app-height);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-width: 16.5625rem;
  min-height: 34.375rem;
  height: 100vh;
  height: var(--app-height);
  font-family: "Josefin Sans", sans-serif;
  background: #e9795d;
  color: #003049;
  letter-spacing: -0.0625rem;
}
@media (min-width: 48em) {
  body {
    min-height: 56.25rem;
  }
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --app-height: 100%;
}

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  max-width: 22.5rem;
  min-width: 16.5625rem;
  height: 95%;
  min-height: 31.25rem;
  max-height: 46.875rem;
  margin: 0 auto;
  background-color: #fff9e7;
  border-radius: 2.8125rem;
  border: solid #f9c74f 0.625rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
@media (min-width: 48em) {
  .container {
    padding: 1.25rem 1.875rem;
    overflow: hidden;
    max-width: none;
    max-height: 59.375rem;
    min-height: 50rem;
    display: grid;
    grid-template-rows: 1fr 6fr;
    grid-template-columns: 1fr;
    justify-items: center;
    align-items: stretch;
  }
}
@media (min-width: 64em) {
  .container {
    max-width: 75rem;
    grid-template-rows: 1fr 7fr;
  }
}

.hidden {
  display: none !important;
}

.out {
  position: absolute;
  left: -99999px;
}
@media (min-width: 48em) {
  .out {
    position: static;
  }
}

@keyframes show {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes show-flex {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: flex;
    opacity: 0;
    transform: scale(0.7);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes show-scale {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
    transform: scale(0.7);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes show-scale-grid {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: grid;
    opacity: 0;
    transform: scale(0.7);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.show {
  display: block !important;
  opacity: 1;
  animation: show 200ms ease-in-out;
}

.show-scale-flex {
  display: flex !important;
  opacity: 1;
  transform: scale(1);
  animation: show 200ms ease-in-out;
}

.show-scale {
  display: block !important;
  opacity: 1;
  transform: scale(1);
  animation: show-scale 200ms ease-in-out;
}

.show-scale-grid {
  display: grid !important;
  opacity: 1;
  transform: scale(1);
  animation: show-scale-grid 200ms ease-in-out;
}

.padding-mobile {
  padding-bottom: 4.375rem;
}
@media (min-width: 48em) {
  .padding-mobile {
    padding-bottom: 0;
  }
}

main {
  overflow: hidden;
  padding: 0.75rem 0.75rem 0.3125rem 0.75rem;
  width: 100%;
}
@media screen and (min-width: 20em) {
  main {
    padding-top: 1.125rem;
  }
}
@media (min-width: 48em) {
  main {
    padding-top: 0;
    display: grid;
    grid-template-columns: 4.5fr 5.5fr;
    max-height: calc(100% - 8.75rem);
    overflow: hidden;
    gap: 1.25rem;
  }
}
@media (min-width: 64em) {
  main {
    grid-template-columns: 4fr 6fr;
    width: 100%;
    gap: 2.5rem;
    padding-top: 0.75rem;
    max-height: none;
    margin-bottom: 3.4375rem;
  }
}

.btn {
  font-family: "Croissant One", cursive;
  letter-spacing: -0.125rem;
  color: #003049;
  border-radius: 1.875rem;
  background-color: #f4c8a6;
  border: solid 1px #e75c3a;
  box-shadow: -3px 4px 0px #e75c3a;
  transition: all 100ms linear;
  cursor: pointer;
}
.btn:hover {
  transform: translate(-3px, 4px);
  box-shadow: none;
}
.btn--start {
  margin-top: 2.25rem;
  font-size: 1.5625rem;
  padding: 0.3125rem 3.25rem;
}
@media screen and (max-width: 20em) {
  .btn--start {
    margin-top: 1.625rem;
  }
}
@media (min-width: 48em) {
  .btn--start {
    display: none;
  }
}
.btn--search {
  color: #e75c3a;
  font-size: 1rem;
  padding: 0.5rem 1.5rem 0.5rem 1.375rem;
}
@media (min-width: 64em) {
  .btn--search {
    box-shadow: none;
    border-radius: 1.875rem;
    border: none;
    border-left: solid 1px #e75c3a;
  }
  .btn--search:hover {
    transform: none;
    background-color: #faba8a;
  }
}
.btn--nav {
  color: #e75c3a;
  border-radius: 100%;
  font-size: 1.5625rem;
  width: 3.125rem;
  height: 3.125rem;
}
@media (min-width: 48em) {
  .btn--nav {
    font-size: 2.5rem;
    width: 5rem;
    height: 5rem;
  }
}
@media (min-width: 64em) {
  .btn--nav {
    width: auto;
    height: 2.5rem;
    font-size: 1rem;
    border-radius: 1.875rem;
    padding: 0 0.9375rem;
  }
}
.btn--bookmark {
  position: absolute;
  font-size: 1.375rem;
  background-color: #f4c8a6;
  color: #e75c3a;
  border: none;
  border-bottom-left-radius: 0.625rem;
  padding: 1.25rem 1.25rem 0.375rem 0.75rem;
  top: -5%;
  left: 100%;
  transform: translateX(-90%);
  cursor: pointer;
  transition: all 250ms ease-in-out;
}
.btn--bookmark:hover {
  transform: translateX(-90%) translate(-3px, 3px);
}
.btn--back {
  position: absolute;
  font-size: 1.375rem;
  background-color: #f4c8a6;
  color: #e75c3a;
  border: none;
  border-bottom-right-radius: 0.625rem;
  padding: 1.25rem 0.75rem 0.375rem 1rem;
  top: -5%;
  left: 0%;
  cursor: pointer;
  transition: all 250ms ease-in-out;
}
.btn--back:hover {
  transform: translate(-3px, -3px);
}
@media (min-width: 48em) {
  .btn--back {
    display: none;
  }
}
.btn--tiny {
  color: #e75c3a;
  background-color: transparent;
  border: none;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 150ms ease-in-out;
}
.btn--tiny:hover {
  transform: translateY(-2px);
}
.btn--recipe {
  color: #e75c3a;
  background-color: #f4c8a6;
  text-align: center;
  margin: 0.5rem auto;
  text-decoration: none;
  border: solid 1px #e75c3a;
  border-radius: 1.875rem;
  gap: 0.5rem;
  display: flex;
  width: 7.8125rem;
  font-size: 1.125rem;
  padding: 0.75rem 1rem 0.5rem;
  transition: all 150ms ease-in-out;
}
.btn--recipe:hover {
  transform: translateY(-2px);
}
.btn--pagination {
  background-color: #f4c8a6;
  color: #e75c3a;
  border: 1px solid #e75c3a;
  padding: 0.375rem 1rem;
  text-align: center;
  line-height: 0.9375rem;
  font-size: 1rem;
  border-radius: 1.25rem;
  cursor: pointer;
  transition: all 150ms ease-in-out;
}
.btn--pagination-prev:hover {
  transform: translateX(-4px);
}
.btn--pagination-next:hover {
  transform: translateX(4px);
}

.error {
  margin: 3.75rem auto;
  margin-left: -0.625rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.error__animation {
  margin-top: -0.75rem;
}
.error__text {
  font-size: 1.25rem;
  width: 12ch;
}

.header {
  max-width: 62.5rem;
  margin-top: 1rem;
}
@media (min-width: 48em) {
  .header {
    display: flex;
    align-items: center;
    gap: 3.4375rem;
    margin-bottom: 1.5rem;
  }
}
@media (min-width: 64em) {
  .header {
    justify-content: space-between;
    width: 100%;
    gap: 0;
    margin-top: 0;
    margin-bottom: 0;
  }
}
.header__search {
  display: none;
  transition: all 500ms ease-in-out;
}
.header__search--mobile {
  width: auto;
  height: auto;
  opacity: 1;
}
@media (min-width: 48em) {
  .header__search {
    width: auto;
    height: auto;
    opacity: 1;
    display: flex;
    gap: 0.625rem;
  }
}
@media (min-width: 64em) {
  .header__search {
    gap: 0;
    justify-items: center;
    background-color: #fff;
    border-radius: 1.875rem;
    border: solid 1px #e75c3a;
    width: auto;
    max-width: 21.25rem;
    box-shadow: -3px 4px 0px #e75c3a;
    line-height: 1.125rem;
    overflow: hidden;
    padding-left: 0.75rem;
    font-family: "Josefin Sans", sans-serif;
    font-size: 1rem;
    color: #003049;
    letter-spacing: -0.0625rem;
    transition: all 100ms ease-in-out;
    -webkit-appearance: none;
  }
  .header__search:hover {
    transform: translate(-3px, 4px);
    box-shadow: none;
  }
}

.title {
  display: flex;
  gap: 0.3125rem;
  justify-content: center;
  align-items: center;
  transition: all 500ms ease-in-out;
}
.title--mobile {
  transform: translateY(-10px);
}
@media (min-width: 48em) {
  .title--mobile {
    transform: none;
  }
}
.title__logo {
  width: 2.8125rem;
  height: 2.8125rem;
}
@media (min-width: 48em) {
  .title__logo {
    width: 3.4375rem;
    height: 3.4375rem;
  }
}
@media (min-width: 64em) {
  .title__logo {
    width: 2.1875rem;
    height: 2.1875rem;
  }
}
.title__text {
  font-family: "Croissant One", cursive;
  font-size: 2.375rem;
  letter-spacing: -0.25rem;
}
@media (min-width: 48em) {
  .title__text {
    font-size: 3.125rem;
  }
}
@media (min-width: 64em) {
  .title__text {
    font-size: 2.5rem;
  }
}

.nav {
  display: none;
  position: absolute;
  bottom: 1.25rem;
  left: 50%;
  transform: translate(-50%);
}
.nav__list {
  display: flex;
  gap: 2.5rem;
}
@media screen and (max-width: 320px) {
  .nav__list {
    gap: 1.875rem;
  }
}
.nav__title {
  display: none;
}
@media (min-width: 48em) {
  .nav {
    display: block;
    position: absolute;
    bottom: 2.375rem;
    left: 50%;
    transform: translate(-50%);
  }
  .nav__list {
    display: flex;
    gap: 15vw;
  }
  .nav__title {
    display: none;
  }
}
@media (min-width: 64em) {
  .nav {
    position: static;
    transform: none;
  }
  .nav__list {
    gap: 0.625rem;
  }
  .nav__title {
    display: inline-block;
    margin-left: 0.3125rem;
  }
}

.bookmarks .message {
  display: none;
}

.recipe {
  display: none;
  border-radius: 1.875rem;
  overflow-y: scroll;
  height: 55vh;
  min-width: 13.875rem;
  max-height: 31.25rem;
  border: solid 2px #e75c3a;
  transform: translateZ(0);
}
@media (min-width: 48em) {
  .recipe {
    margin: 0 auto;
    width: 100%;
    max-width: none;
    max-height: none;
    height: auto;
  }
}
.recipe__fig {
  width: 100%;
  height: 23vh;
  min-height: 9.375rem;
  position: relative;
  border-top-left-radius: 1.875rem;
  border-top-right-radius: 1.875rem;
  overflow: hidden;
  -webkit-clip-path: content-box;
  clip-path: content-box;
}
@media (min-width: 48em) {
  .recipe__fig {
    max-height: 15.625rem;
  }
}
@media (min-width: 64em) {
  .recipe__fig {
    min-height: 17.5rem;
  }
}
.recipe__img {
  width: 100%;
  display: block;
  height: 100%;
  object-fit: cover;
}
.recipe__title {
  position: absolute;
  text-transform: capitalize;
  text-align: center;
  width: 100%;
  background: rgba(255, 249, 231, 0.65);
  padding: 0.75rem 0.5rem;
  bottom: 0;
  left: 0;
  font-size: 1.5rem;
}
@media (min-width: 48em) {
  .recipe__title {
    font-size: 1.75rem;
  }
}
@media (min-width: 64em) {
  .recipe__title {
    font-size: 2rem;
  }
}
.recipe__title .text {
  text-transform: uppercase;
  display: inline-block;
  vertical-align: middle;
  margin-bottom: -0.375rem;
}
@media (min-width: 64em) {
  .recipe__title .text {
    margin-bottom: -0.5rem;
  }
}
.recipe__user-generated {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  line-height: 1.5rem;
  margin-left: 0.3125rem;
  letter-spacing: 0;
  width: 1.75rem;
  height: 1.75rem;
  background: #f9c74f;
  border-radius: 50%;
  font-size: 1.125rem;
}
@media (min-width: 64em) {
  .recipe__user-generated {
    width: 2.125rem;
    height: 2.125rem;
    font-size: 1.25rem;
  }
}
.recipe__details {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0.75rem auto;
}
@media (min-width: 64em) {
  .recipe__details {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 30px;
  }
}
.recipe__info {
  font-size: 1.25rem;
  margin-top: 0.5rem;
}
.recipe__info:nth-child(2) {
  margin-top: 0.375rem;
}
.recipe__info-buttons {
  display: inline-flex;
  margin-left: 0.3125rem;
  gap: 0.625rem;
}
.recipe .heading--2 {
  text-align: center;
  text-decoration: underline;
  font-size: 1.5rem;
}
.recipe__ingredients, .recipe__directions {
  margin: 1.5rem 1.125rem;
}
.recipe__ingredient-list {
  font-size: 1.125rem;
  margin: 1.5rem auto;
  max-width: 15rem;
}
@media (min-width: 64em) {
  .recipe__ingredient-list {
    margin: 1.5rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: flex-start;
    justify-items: center;
    align-items: center;
    column-gap: 1.875rem;
    max-width: none;
  }
}
.recipe__ingredient {
  margin: 0.75rem auto;
  display: grid;
  grid-template-columns: 1fr 2fr 7fr 2fr;
  width: 100%;
}
.recipe__quantity {
  justify-self: center;
}
.recipe__directions-text {
  text-align: center;
  font-size: 1.125rem;
  margin: 1rem auto;
}

.results {
  display: none;
  grid-template-rows: 1fr;
  grid-template-columns: 100%;
  transition: all 500ms ease-in-out;
  background-color: #fff;
  border-radius: 1.875rem;
  height: 55vh;
  overflow-y: scroll;
  border: solid 2px #e75c3a;
  max-height: 30.625rem;
}
@media (min-width: 48em) {
  .results {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 100%;
    width: 100%;
    opacity: 1;
    background-color: #fff;
    border-radius: 1.875rem;
    height: 100%;
    margin: 0 auto;
    max-height: none;
    border: solid 2px #e75c3a;
  }
}
@media (min-width: 64em) {
  .results {
    max-height: none;
  }
}
.results .message {
  text-align: center;
  align-self: flex-start;
  padding: 0.625rem;
}
.results .message__title {
  margin-top: 2.5rem;
  padding: 0 1rem;
  font-size: 2.5rem;
  font-weight: 500;
  letter-spacing: -0.125rem;
}
@media (min-width: 64em) {
  .results .message__title {
    margin-top: 5rem;
  }
}
.results .message .animation {
  margin: -2.5rem auto 0;
}
.results__list {
  list-style: none;
  width: 100%;
}
.results .result {
  width: 90%;
  margin: 1rem auto;
  font-size: 0.625rem;
  background-color: #fff9e7;
  border: 2px solid #e75c3a;
  box-shadow: -3px 4px 0px #e75c3a;
  border-radius: 0.9375rem;
  transition: all 100ms linear;
  cursor: pointer;
}
.results .result:hover {
  transform: translate(-3px, 4px);
  box-shadow: none;
}
.results .result__link {
  padding: 0.5rem 1rem;
  display: flex;
  text-decoration: none;
  align-items: center;
  gap: 0.75rem;
}
.results .result__fig {
  flex: 0 0 3.75rem;
  border-radius: 50%;
  height: 3.75rem;
  overflow: hidden;
}
@media screen and (max-width: 320px) {
  .results .result__fig {
    flex: 0 0 2.5rem;
    height: 2.5rem;
  }
}
.results .result__fig img {
  height: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
}
.results .result__data {
  color: #003049;
  width: 100%;
  overflow: hidden;
}
.results .result__title {
  font-size: 1.125rem;
  margin-top: 0.25rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: capitalize;
  font-weight: 600;
}
.results .result__publisher {
  margin-top: 0.25rem;
  font-size: 1rem;
  font-weight: 300;
}
@media (min-width: 64em) {
  .results .result__title {
    font-size: 1.25rem;
  }
  .results .result__publisher {
    font-size: 1.125rem;
  }
}
.results__pagination {
  margin: 0 auto 0.5rem;
  display: grid;
  place-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  width: 80%;
}
.results .page {
  letter-spacing: 0;
  line-height: 2.5rem;
  background: #f4c8a6;
  text-align: center;
  color: #003049;
  font-size: 1rem;
  font-weight: 700;
  font-family: "Croissant One", cursive;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  grid-column: 2/3;
}

.search {
  transition: all 500ms ease-in-out;
}
.search__field {
  background-color: #fff;
  border-radius: 1.875rem;
  border: solid 1px #e75c3a;
  width: 47vw;
  min-width: 8.75rem;
  max-width: 13.25rem;
  margin-right: 0.5rem;
  box-shadow: -3px 4px 0px #e75c3a;
  line-height: 1.125rem;
  padding: 0.75rem 1rem 0.5rem 1rem;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1rem;
  color: #003049;
  letter-spacing: -0.0625rem;
  transition: all 100ms ease-in-out;
  -webkit-appearance: none;
}
@media (min-width: 48em) {
  .search__field {
    max-width: none;
    width: 13.125rem;
  }
}
@media (min-width: 64em) {
  .search__field {
    background-color: none;
    border-radius: 0;
    border: none;
    width: 18vw;
    max-width: 18.75rem;
    box-shadow: none;
    line-height: 1.125rem;
    padding: 0;
    font-family: "Josefin Sans", sans-serif;
    font-size: 1rem;
    color: #003049;
    letter-spacing: -0.0625rem;
    transition: all 100ms ease-in-out;
    -webkit-appearance: none;
  }
}
.search__field:focus {
  transform: translate(-3px, 4px);
  box-shadow: none;
  border: solid 2px #f9c74f;
  outline: none;
  overflow: visible;
}
.search__field:focus::placeholder {
  visibility: hidden;
}
@media (min-width: 64em) {
  .search__field:focus {
    border: none;
    outline: none;
    transform: none;
  }
}
.search__field::placeholder {
  color: #003049;
  line-height: 1.125rem;
  text-overflow: ellipsis;
  opacity: 0.5;
}
.search__text {
  display: none;
}

.spinner {
  font-size: 4.375rem;
  color: #e75c3a;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.welcome {
  margin-top: 2.25rem;
  text-align: center;
}
@media screen and (max-width: 20em) {
  .welcome {
    margin-top: 1.625rem;
  }
}
@media (min-width: 48em) {
  .welcome {
    margin-top: 4.375rem;
  }
}
@media (min-width: 64em) {
  .welcome {
    margin-top: 6rem;
  }
}
.welcome__title {
  font-size: 1.75rem;
}
@media (min-width: 48em) {
  .welcome__title {
    font-size: 2.5rem;
  }
}
.welcome__text {
  margin: 0.9375rem auto 0;
  font-size: 1.125rem;
  width: 18ch;
}
@media (min-width: 48em) {
  .welcome__text {
    font-size: 1.25rem;
  }
}
.welcome__subtitle {
  font-size: 1.375rem;
  margin-top: 1.75rem;
}
@media screen and (max-width: 20em) {
  .welcome__subtitle {
    margin-top: 1.25rem;
  }
}
@media (min-width: 48em) {
  .welcome__subtitle {
    font-size: 1.5625rem;
  }
}
.welcome__features {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.features__item {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  gap: 1.5625rem;
}
@media (min-width: 48em) {
  .features__item {
    margin-top: 1.375rem;
  }
}
.features__icon {
  color: #e75c3a;
  font-size: 1.375rem;
}
@media (min-width: 48em) {
  .features__icon {
    font-size: 1.625rem;
  }
}
.features__text {
  width: 15ch;
  text-align: left;
}
@media (min-width: 48em) {
  .features__text {
    font-size: 1.125rem;
  }
}